@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat/montserrat-regular.woff2"), url("../fonts/montserrat/montserrat-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat/montserrat-medium.woff2"), url("../fonts/montserrat/montserrat-medium.woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat/montserrat-semibold.woff2"), url("../fonts/montserrat/montserrat-semibold.woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat/montserrat-bold.woff2"), url("../fonts/montserrat/montserrat-bold.woff");
  font-display: swap;
}
@font-face {
  font-family: "MontserratAlternates";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserratalternates/montserratalternates-regular.woff2"), url("../fonts/montserratalternates/montserratalternates-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/inter/inter-regular.woff2"), url("../fonts/inter/inter-regular.woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 375px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.6);
  background-color: #ffffff;
}

.container {
  width: 100%;
  max-width: 1180px;
  padding: 0 16px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.main-header__wrapper {
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-header__wrapper {
    margin: 0 -16px;
  }
}

.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

@media (min-width: 1180px) {
  .main-header__logo-img {
    width: 237px;
    height: 52px;
  }
}

.main-header__contacts {
  margin-top: auto;
  z-index: 3;
  display: none;
}
@media (max-width: 767px) {
  .main-header__contacts {
    display: block;
  }
}
.main-header__contacts--index {
  display: block;
}
@media (min-width: 768px) {
  .main-header__contacts--index {
    position: absolute;
    top: 100px;
    right: 0;
    max-width: 358px;
  }
}

.main-header__contacts-box {
  display: flex;
  flex-direction: column;
}

.main-header__contacts-item {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.main-header__contacts-img {
  margin-right: 12px;
  min-width: 26px;
  height: 26px;
}
@media (min-width: 768px) {
  .main-header__contacts-img {
    min-width: 32px;
    height: 32px;
  }
}

.main-header__contacts-text {
  font-style: normal;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.8);
}
@media (min-width: 768px) {
  .main-header__contacts-text {
    color: #ffffff;
  }
}

.main-header__contacts-socials {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px 0;
}
@media (max-width: 767px) {
  .main-header__contacts-socials {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .main-header__contacts-socials {
    padding: 0;
  }
}

.main-header__contacts-social {
  display: flex;
  align-items: center;
}

.main-header__contacts-link:hover {
  opacity: 0.6;
}
.main-header__contacts-link:active {
  opacity: 0.3;
}

.main-nav {
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.main-nav__wrapper {
  z-index: 3;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    position: absolute;
    display: none;
    left: 0;
    right: 0;
    padding: 0 16px;
    min-height: 576px;
    background-color: #ffffff;
  }
}

.main-nav__wrapper--opened {
  display: flex;
}

.main-nav__top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
}
@media (min-width: 768px) {
  .main-nav__top-wrapper {
    padding: 12px 0 7px 0;
  }
}

.main-nav__list {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 24px;
  margin: 0;
  padding: 18px 0 0 0;
  list-style: none;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav__list {
    grid-template-columns: unset;
    gap: 24px;
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
}

@media (min-width: 768px) {
  .main-nav__item:last-child {
    order: -1;
  }
}

.main-nav__link {
  display: block;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.8);
}
.main-nav__link[href]:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 8px 14px;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    background-color: #9edc14;
    border: 2px solid #abe921;
    box-shadow: 0 2px 12px #d1d1d1;
  }
  .main-nav__link[href]:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .main-nav__link[href]:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav__toggle {
  position: relative;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:disabled {
  opacity: 0.3;
}

.main-nav__toggle--opened .main-nav__toggle-line:nth-child(1) {
  top: 50%;
  left: 0;
  transform: rotate(45deg);
}
.main-nav__toggle--opened .main-nav__toggle-line:nth-child(2) {
  top: 50%;
  left: 0;
  transform: rotate(-45deg);
}

.main-nav__toggle-line {
  display: block;
  position: absolute;
  left: 50%;
  width: 16px;
  height: 2px;
  background-color: #6f767e;
  transition: 0.3s ease;
  transform: translateX(-50%);
}
.main-nav__toggle-line:nth-child(1) {
  top: 7px;
}
.main-nav__toggle-line:nth-child(2) {
  top: 14px;
}

.hero {
  padding: 96px 0 133px 18%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url("../img/hero-bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero {
    padding: 208px 0 309px 32px;
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.55) 35.96%, #000000 101.42%), url("../img/hero-bg-desktop@1x.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.55) 35.96%, rgba(0, 0, 0, 0) 101.42%), url("../../img/hero-bg-desktop@2x.jpg");
  }
}

.hero__text-wrapper {
  position: relative;
  max-width: 277px;
}
@media (min-width: 1180px) {
  .hero__text-wrapper {
    max-width: 633px;
    margin-left: 15px;
  }
}
.hero__text-wrapper::after {
  content: "";
  position: absolute;
  top: -24px;
  left: -23px;
  width: 257px;
  height: 357px;
  background-image: url("../img/sprite.svg#hero-bg");
  z-index: 2;
}
@media (min-width: 1180px) {
  .hero__text-wrapper::after {
    top: -42px;
    left: -38px;
    width: 370px;
    height: 520px;
  }
}

.hero__title {
  margin: 0 0 16px 0;
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  color: #ffffff;
}
@media (min-width: 1180px) {
  .hero__title {
    font-size: 54px;
    line-height: 66px;
  }
}

.hero__lead {
  max-width: 211px;
  margin: 0;
  font-weight: 500;
  color: #ffffff;
}
@media (min-width: 1180px) {
  .hero__lead {
    max-width: 300px;
    font-size: 20px;
    line-height: 24px;
  }
}